"use client";
import { useState, useEffect } from 'react';
import { Copy, ExternalLink, Play } from 'lucide-react';
import Link from 'next/link';

type Client = {
  id: string;
  name: string;
  company: string;
};

type Presentation = {
  id: string;
  title: string;
  clientId: string;
  token: string;
  googleSlidesUrl: string;
  ploomesDealId?: string;
  createdAt: string;
};

export default function PresentationsPage() {
  const [clients, setClients] = useState<Client[]>([]);
  const [presentations, setPresentations] = useState<Presentation[]>([]);
  const [formData, setFormData] = useState({ title: '', clientId: '', googleSlidesUrl: '', ploomesDealId: '' });
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    fetchData();
  }, []);

  const fetchData = async () => {
    try {
      const [clientsRes, presRes] = await Promise.all([
        fetch('http://localhost:3001/api/clients'),
        fetch('http://localhost:3001/api/presentations')
      ]);
      setClients(await clientsRes.json());
      setPresentations(await presRes.json());
    } catch (error) {
      console.error('Failed to fetch data', error);
    } finally {
      setIsLoading(false);
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    // Convert Edit URL to Embed URL automatically
    let embedUrl = formData.googleSlidesUrl;
    if (embedUrl.includes('/edit')) {
      embedUrl = embedUrl.replace('/edit', '/embed?start=false&loop=false&delayms=3000');
      // Remove any query params after /edit if simpler regex needed, but simple replace works for standard docs
    }

    try {
      const res = await fetch('http://localhost:3001/api/presentations', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ ...formData, googleSlidesUrl: embedUrl })
      });
      if (res.ok) {
        setFormData({ title: '', clientId: '', googleSlidesUrl: '', ploomesDealId: '' });
        fetchData();
      }
    } catch (error) {
      console.error('Failed to create presentation', error);
    }
  };

  const getClientName = (id: string) => clients.find(c => c.id === id)?.name || 'Unknown';
  const getPublicLink = (token: string) => `${window.location.origin}/pub/${token}`;

  const copyToClipboard = (text: string) => {
    navigator.clipboard.writeText(text);
    alert('Link copiado!');
  };

  return (
    <div>
      <h2 className="text-3xl font-bold mb-8">Gestão de Apresentações</h2>

      {/* Create Presentation Form */}
      <div className="bg-brand-darkgray p-6 rounded-2xl mb-12 border border-white/10">
        <h3 className="text-xl font-bold mb-4 text-brand-neon">Nova Apresentação</h3>
        <form onSubmit={handleSubmit} className="grid grid-cols-1 md:grid-cols-2 gap-4">
          <div className="md:col-span-2">
            <label className="block text-xs uppercase text-brand-gray mb-1">Título</label>
            <input
              type="text"
              placeholder="Ex: Proposta Comercial - Q1"
              value={formData.title}
              onChange={e => setFormData({ ...formData, title: e.target.value })}
              className="w-full bg-black/50 border border-white/10 rounded-lg p-3 text-white focus:outline-none focus:border-brand-neon"
              required
            />
          </div>

          <div>
            <label className="block text-xs uppercase text-brand-gray mb-1">Cliente</label>
            <select
              value={formData.clientId}
              onChange={e => setFormData({ ...formData, clientId: e.target.value })}
              className="w-full bg-black/50 border border-white/10 rounded-lg p-3 text-white focus:outline-none focus:border-brand-neon"
              required
            >
              <option value="">Selecione um cliente...</option>
              {clients.map(client => (
                <option key={client.id} value={client.id}>{client.name} - {client.company}</option>
              ))}
            </select>
          </div>

          <div>
            <label className="block text-xs uppercase text-brand-gray mb-1">Link do Google Slides (Edit)</label>
            <input
              type="url"
              placeholder="https://docs.google.com/presentation/d/.../edit"
              value={formData.googleSlidesUrl}
              onChange={e => setFormData({ ...formData, googleSlidesUrl: e.target.value })}
              className="w-full bg-black/50 border border-white/10 rounded-lg p-3 text-white focus:outline-none focus:border-brand-neon"
              required
            />
          </div>

          <div className="md:col-span-2">
            <label className="block text-xs uppercase text-brand-gray mb-1">ID do Negócio no Ploomes (Opcional)</label>
            <input
              type="text"
              placeholder="Ex: 123456"
              value={formData.ploomesDealId}
              onChange={e => setFormData({ ...formData, ploomesDealId: e.target.value })}
              className="w-full bg-black/50 border border-white/10 rounded-lg p-3 text-white focus:outline-none focus:border-brand-neon"
            />
            <p className="text-xs text-brand-gray mt-1">Se preenchido, enviaremos atualizações para este negócio quando a apresentação for visualizada.</p>
          </div>

          <div className="md:col-span-2 flex justify-end mt-4">
            <button type="submit" className="bg-brand-neon text-black font-bold py-3 px-6 rounded-lg hover:shadow-glow transition-all">
              Gerar Link Público
            </button>
          </div>
        </form>
      </div>

      {/* List */}
      <div className="grid grid-cols-1 gap-4">
        {presentations.map(pres => (
          <div key={pres.id} className="bg-brand-darkgray p-6 rounded-xl border border-white/5 flex flex-col md:flex-row items-start md:items-center justify-between gap-4">
            <div>
              <h4 className="text-lg font-bold text-white">{pres.title}</h4>
              <p className="text-brand-gray text-sm">Cliente: <span className="text-white">{getClientName(pres.clientId)}</span></p>
              <div className="flex items-center gap-2 mt-2">
                <code className="bg-black px-2 py-1 rounded text-brand-neon text-xs">{pres.token}</code>
                <span className="text-xs text-gray-500">{new Date(pres.createdAt).toLocaleDateString()}</span>
              </div>
            </div>

            <div className="flex gap-3">
              <button
                onClick={() => copyToClipboard(getPublicLink(pres.token))}
                className="flex items-center gap-2 px-4 py-2 rounded-lg bg-white/5 hover:bg-white/10 transition-colors text-sm font-medium"
              >
                <Copy size={16} /> Copiar Link
              </button>
              <a
                href={getPublicLink(pres.token)}
                target="_blank"
                className="flex items-center gap-2 px-4 py-2 rounded-lg bg-white/5 hover:bg-white/10 transition-colors text-sm font-medium"
              >
                <ExternalLink size={16} /> Testar
              </a>
              <Link
                href={`/admin/analytics/${pres.token}`}
                className="flex items-center gap-2 px-4 py-2 rounded-lg bg-brand-neon/10 text-brand-neon hover:bg-brand-neon/20 transition-colors text-sm font-medium border border-brand-neon/20"
              >
                <Play size={16} /> Ver Dashboard
              </Link>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}
